<template>
  <wrapper titulo="Menús">
    <v-row v-if="$usuario.hijos && $usuario.hijos.length > 0">
      <v-col cols="12" class="text-center">
        <panel-opciones :alumno="hijoSeleccionado.alumno" :fecha="fecha" @cambiar-fecha="fecha = $event"
                        @seleccionar-hijo="seleccionarHijo" :saldo="saldoCafeteriaAlumnoSeleccionado" />
      </v-col>
      <v-col cols="12" class="text-center">
        <h2 v-if="hijoSeleccionado" class="text--disabled">{{hijoSeleccionado.alumno.cafeteria.nombre}}</h2>
      </v-col>
      <v-col v-if="menusFiltrados.length > 0" cols="12">
        <v-fade-transition mode="out-in" hide-on-leave>
        <carrusel :key="hijoSeleccionado.id" per-page-escritorio="2">
          <carrusel-item v-for="(menu, indexMenu) in menusFiltrados" :key="indexMenu">
            <tarjeta-menu :menu="menu" :opcion-agregar="!estaMenuEnCarrito(menu.id)" :cargando="hijoSeleccionado.cargandoSaldo" mostrar-acciones
                            @seleccionar="seleccionarProducto(hijoSeleccionado.alumno, fecha, menu)"/>
          </carrusel-item>
        </carrusel>
        </v-fade-transition>
      </v-col>
      <v-col v-else cols="12">
        <v-row align="center" justify="center" style="display: flex;justify-content: center;align-items: center;">
          <v-col cols="12" class="text-center" style="display: flex;justify-content: center;align-items: center;">
            <img src="../../assets/menu_estado_vacio.svg"
                 :style="$vuetify.breakpoint.smAndDown ? 'width:100%' : 'width:50%'"/>
          </v-col>
          <v-col cols="12" class="text-center">
            <h2 style="font-family: Nunito;" class="text--disabled">No hay menús disponibles para este día</h2>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
    <v-row v-else align="center" justify="center" style="display: flex;justify-content: center;align-items: center;">
      <v-col cols="12" class="text-center" style="display: flex;justify-content: center;align-items: center;">
        <img src="../../assets/menu_estado_vacio.svg" :style="$vuetify.breakpoint.smAndDown ? 'width:100%' : 'width:50%'"/>
      </v-col>
      <v-col cols="12" class="text-center">
        <h2 style="font-family: Nunito;" class="text--disabled">No tiene alumnos agregados</h2>
      </v-col>
      <v-col cols="12" md="6" class="text-center">
        <v-btn rounded :to="{ name: 'Alumnos' }"><span style="font-family: Nunito;" class="primary--text">Ir a alumnos</span></v-btn>
      </v-col>
    </v-row>
    <v-snackbar v-model="mensajeProductoAgregado" color="success" bottom app>Producto agregado</v-snackbar>
  </wrapper>
</template>

<script>
  import TarjetaMenu from "./TarjetaMenu";
  import MenusService from "../../services/MenusService";
  import Carrusel from "../General/Carrusel/Carrusel";
  import CarruselItem from "../General/Carrusel/CarruselItem";
  import Orden from "../../models/Orden";
  import AuthConst from "../../config/AuthConst";
  import FechaService from "../../services/FechaService";
  import CafeteriasService from "../../services/CafeteriasService";
  import SaldoService from "../../services/SaldoService";
  import OrdenesService from "../../services/OrdenesService";
  import PanelOpciones from "../General/PanelDeOpciones/PanelOpciones";

  export default {
    name: "DashboardMenu",
    components: {PanelOpciones, CarruselItem, Carrusel, TarjetaMenu},
    data() {
      return {
        calendario: false,
        fecha: null,
        dialogSeleccionarAlumno: false,
        menus: [],
        ordenes: [],
        hijoSeleccionado: {},
        mensajeProductoAgregado: false,
        menusFiltrados: []
      }
    },
    async created() {
      this.hijoSeleccionado = this.$usuario.hijos.length ? this.$usuario.hijos[0] : {}
      this.$loader = true
      try {
        this.menus = await MenusService.getMenusSemanales()
      } catch (e) {
        this.menus = []
      }

      try {
        this.ordenes = await OrdenesService.getOrdenesPorCliente(this.$usuario.id)
      } catch (e) {
        console.log(e)
      }

      this.fecha = FechaService.getFechaString()
      this.$loader = false

      const vm = this
      for (const hijo of this.$usuario.hijos) {
        vm.$set(hijo, 'cargandoSaldo', true)
        CafeteriasService.getSaldoPorCafeteria(vm.$usuario.idCliente, hijo.alumno.id)
          .then(cafeterias => {
            vm.$set(hijo.alumno.cafeteria, 'saldoAlumno',
              vm.$getSaldoDeAlumnoParaCafeteria(hijo.alumno.cafeteria.id, cafeterias))
          })
          .catch(() => {})
          .finally(() => (vm.$set(hijo, 'cargandoSaldo', false)))
      }
    },
    watch: {
      async fecha(val) {
        this.$loader = true;
        try {
          this.filtrarMenu(val)
        } catch (e) {
          console.log(e)
        }
        this.$loader = false;
      }
    },
    computed: {
      saldoCafeteriaAlumnoSeleccionado() {
        return this.hijoSeleccionado.alumno.cafeteria.saldoAlumno
          - SaldoService.getCostoTotalOrdenes(this.hijoSeleccionado.alumno, this.ordenes.filter(orden => orden.estatus.titulo === 'CREADA'))
          - SaldoService.getCostoTotalOrdenes(this.hijoSeleccionado.alumno, this.$ordenesNuevas)
      }
    },
    methods: {
      seleccionarProducto(alumno, fecha, menu) {
        if (existeMenuEnOrdenAlumno(this, menu, alumno, fecha)) {
          this.$showAlerta('600', 'warning', 'mdi-alert-circle',
            'Sólo se puede agregar un menú por día', false);
        } else {
          for (let detalle of menu.detalles) {
            if (!SaldoService.esSaldoSuficiente(this.$ordenesNuevas, alumno, detalle.producto, detalle.cantidad)) {
              this.$showAlerta('600', 'warning', 'mdi-alert-circle',
                'El alumno no cuenta con saldo suficiente', false);
            } else if (alumno.productosBloqueados.some(pb => detalle.producto.id === pb.producto.id)) {
              this.$showAlerta('600', 'warning', 'mdi-alert-circle',
                'El producto ' + detalle.producto.nombre + ' no se ha agregado a la orden porque se encuentra bloqueado para ' + alumno.nombre, false);
            } else {
              asignarProductoEnOrden(this, alumno, fecha, { ...detalle, menu})
            }
          }
        }
      },
      seleccionarHijo(hijo) {
        this.hijoSeleccionado = hijo
        this.filtrarMenu(this.fecha)
      },

      filtrarMenu(fechaSeleccionada) {
        if (fechaSeleccionada && this.hijoSeleccionado.id) {
          const vm = this
          this.menusFiltrados = vm.menus.filter(menu => {
            return (menu.esSemanal || fechaSeleccionada === menu.fecha)
              && (!vm.hijoSeleccionado.alumno.cafeteria || vm.hijoSeleccionado.alumno.cafeteria.id === menu.cafeteria.id)
              && (vm.hijoSeleccionado.alumno.seccion.id === menu.seccionEscolar.id)
          })
        } else
          this.menusFiltrados = [ ]
      },
      estaMenuEnCarrito(idMenu) {
        return this.$ordenesNuevas.some(
          orden => this.fecha === orden.fecha
            && this.hijoSeleccionado.alumno.id === orden.alumno.id
            && orden.productos.some(producto => producto.menu && idMenu === producto.menu.id)
        )
      }
    }
  }

  function asignarProductoEnOrden(vm, alumno, fecha, detalle) {
    let index = vm.$ordenesNuevas.findIndex(orden => orden.idAlumno === alumno.id && orden.fecha === fecha)
    if (index < 0)
      index = (vm.$ordenesNuevas.push(new Orden({
        alumno,
        fecha,
        cafeteria: alumno.cafeteria,
        clienteInternet: vm.$usuario
      }))) - 1

    const orden = vm.$ordenesNuevas[index]
    orden.productos.push({
      id: 0, cantidad: detalle.cantidad, costo: detalle.cantidad * detalle.producto.costo,
      menu: detalle.menu,
      producto: detalle.producto
    })
    vm.$set(orden, 'costo', orden.productos.reduce((total, p) => total + p.costo, 0))

    vm.mensajeProductoAgregado = true
    localStorage.setItem(AuthConst.CARRITO_KEY, JSON.stringify(vm.$ordenesNuevas))

  }

  function SeleccionarMenuPorFecha(vm, fecha) {

  }

  function existeMenuEnOrdenAlumno(vm, menu, alumno, fecha) {
    return vm.$ordenesNuevas
      .some(orden => fecha === orden.fecha && alumno.id === orden.alumno.id && orden.productos.some(producto => !!(producto.menu)))
  }
</script>

